import { fetchEndpoint } from '../../network/network';

//     app
export const authWeb =
(vendor, scope, referer) =>
fetchEndpoint(
  '/v2/ecapi.auth.web',
  'GET',
  {
    "vendor": vendor,  // 
    "scope": scope, 
    "referer": referer,  //  (URL ENCODE)
  }
)




