export default {

	fetch(key){
		return JSON.parse(window.localStorage.getItem(key)||'[]')
	},
	save(key, value){
		window.localStorage.setItem(key, JSON.stringify(value))
	},
	stopPrevent(event) {
		let e = event || window.event;
		if(e.preventDefault){
			e.preventDefault();
		}else{
	        window.event.returnValue = false;//IE
	    }
	},
	/*
	 *  arrayFilter: 
	 */
	arrayFilter(array) {
		let newAray = [];
		for (let i = 0, len = array.length-1; i <= len; i++) {
			if (newAray.indexOf(array[i]) < 0){
				newAray.push(array[i]);
			}
		}
		return newAray;
	},

	fillTheScreen(obj) {
		const isWX = /micromessenger/.test(navigator.userAgent.toLowerCase())
	  	// why? document.documentElement.clientHeight - document.documentElement.offsetHeight
	  	let height = isWX ? document.documentElement.clientHeight : document.documentElement.offsetHeight
	  	if (!obj.target || !obj.totalHeight) return
	  	height = 1 - obj.totalHeight / height
	  	obj.target.style.height = height * 100 + 'vh'
	},

	/*
		*@param start  
		*@param end 
		*@param target 
	*/
	replaceStr(target,  start, end, length) {
		let str = '';
		if (start) {
			str = target.substr(start, length) + '***';
		} else if (end) {
			str = '***'+ target.substr(end, length);
		} else {
			str = target.substr(0, 1) + '***'+ target.substr(target.length-1, 1);
		}
		return str;
	},

	/*
		date 
		fmt 
	*/
	formatDate(date, fmt) {
		if (/(y+)/.test(fmt)) {
			fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
		}
		let o = {
			'M+': date.getMonth() + 1,
			'd+': date.getDate(),
			'h+': date.getHours(),
			'm+': date.getMinutes(),
			's+': date.getSeconds()
		};
		for (let k in o) {
			if (new RegExp(`(${k})`).test(fmt)) {
				let str = o[k] + '';
				fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str));
			}
		}
		return fmt;
	},

	padLeftZero(str) {
		return ('00' + str).substr(str.length);
	},

	/*
		price 
		
	*/
	currencyPrice(price) {
		return parseFloat(price).toFixed(2)
	},

	/**
	 * value 
	 * 
	 */
	isNumber(value) {
		let reg = new RegExp('^[0-9]*$')
		if (!reg.test(value)) {
			return false
		}
		return true
	},

	/**
	 * beginAt 
	 * endAt 
	 */
	activityStatus (beginAt, endAt) {
		let status = -1 // (0: 1: 2: )
		let timestamp = Date.parse(new Date()) / 1000
		if (beginAt > timestamp) {
			status = 0
		} else if (timestamp > beginAt && timestamp < endAt) {
			status = 1
		} else if (timestamp > endAt) {
			status = 2
		}
		return status
	},

	/**
	 * interval s
	 * ****
	 */
	formatTimeInterval (interval) {
		let format = null
		let day = parseInt(interval / 60.0 / 60.0 / 24.0)
		let hour = parseInt(interval / 60 / 60 % 24)
		let minute = parseInt(interval / 60 % 60)
		let second = interval % 60
		format = day + '  ' + hour + '  ' + minute + '  ' + second + ' '
		return format
	},

	/*
	 * getOpenBrowser: 
	 */
	getOpenBrowser() {
		let browser = {
			versions: function () {
			  	var u = navigator.userAgent,
			  		app = navigator.appVersion;
			  	return {   //
				   trident: u.indexOf('Trident') > -1, //IE
				   presto: u.indexOf('Presto') > -1, //opera
				   webKit: u.indexOf('AppleWebKit') > -1, //
				   gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //
				   mobile: !!u.match(/AppleWebKit.*Mobile.*/), //
				   ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
				   android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
				   iPhone: u.indexOf('iPhone') > -1, //iPhoneQQHD
				   iPad: u.indexOf('iPad') > -1, //iPad
				   webApp: u.indexOf('Safari') == -1 //web
			  	};
			}(),
			language: (navigator.browserLanguage || navigator.language).toLowerCase()
		};
		let browserHeight = 0;
		if (browser.versions.mobile) {//
		    let ua = navigator.userAgent.toLowerCase();//
		    //
			if (ua.match(/MicroMessenger/i) == "micromessenger") {}
			//
		    if (ua.match(/WeiBo/i) == "weibo") {}
		    //QQ
		  	if (ua.match(/QQ/i) == "qq") {}
		  	//IOS
		  	if (browser.versions.ios) {
		  		browserHeight = 44;
		  	}
		  	//
		    if(browser.versions.android){
		    	browserHeight = 44;
		    }
		} else {
		  //PC
		}
		return browserHeight;
	}
}



