<template>
  <div class="container">
     <!-- header -->
    <mt-header class="header" title="">
      <header-item slot="left" v-bind:isBack=true v-on:onclick="goBack">
      </header-item>
    </mt-header>
    <!-- body -->
    <div class="service-Message-body">
    	<a href="https://www.sobot.com/chat/h5/index.html?sysNum=5b696ae2d5394f06b558e64ebbaf3be7&back=1&titleFlag=2&customTtile='' ">
	        <img class="photo" src="../../../assets/image/change-icon/avatar_service.png">
	        <div class="right-wrapper">
	          <div>
	            <span class="title"></span>
	            <p class="message-number">
	              <span>6</span>
	            </p>
	          </div>
	          <p class="content">...</p>
	        </div>
	    </a>
      </div>
  </div>
</template>

<script>
  export default {
    methods: {
      goBack() {
        this.$router.go(-1);
      },
      goService() {
        console.log('')
      }
    }
  }
</script>

<style lang="scss" scoped>
  .container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    .header {
      @include header;
      border-bottom: 1px solid #E8EAED;
    }
    .service-Message-body {
    	a {
			 display: flex;
	      flex-direction: row;
	      justify-content: flex-start;
	      align-items: stretch;
    	}
      background-color: #fff;
      border-bottom: 1px solid #E8EAED;
      .photo {
        width:50px;
        height:50px;
        padding:15px;
      }
      .right-wrapper {
        padding:15px 19px 15px 0px;
        overflow: hidden;
        >div {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .title {
            color: #4E545D;
            font-size: 14px;
          }
          .message-number {
            display: block;
            top: 5px;
            right: 20px;
            width: 16px;
            height: 16px;
            background-color: #F33C3C;
            line-height: 16px;
            text-align: center;
            border-radius: 30px ;
            span {
              font-size: 12px;
              color:#fff;
            }
          }
        }
        .content {
          color: #7C7F88;
          font-size: 12px;
          margin-top:10px;

          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }
</style>




